@charset "utf-8";

/* Reset CSS */
body, h1, h2, h3, h4, h5, h6, p, ul, ol, table, dl, dd {
	margin: 0;
	padding: 0;
	font-family: "Roboto", "Noto Sans KR", "Tourney", "Teko", sans-serif;
}
h1 {
	font-size: 16px;
}
a {
	text-decoration: none;
}
ul, ol {
	list-style: none;
}
img {
	max-width: 100%;
	vertical-align: top;
	border: none;
}

body,
body  .main_wrapper {
	transition: background-color 0.8s;
	background-color: transparent;
	overflow-x: hidden;
}

body.on .main_wrapper {
	/* background: linear-gradient(-45deg, #333333, #2b2b2b, #424242, #a7a7a7); */
	/* background-color: #333; */
	background-color: #000;
}
body.fixed .main_wrapper {
	overflow-y: hidden;
}
.circle {
	z-index: 70000000000000000;
	position: absolute;
	width: 20px;
	height: 20px;
	/* top: 50%; */
	/* left: 50%; */
	/* margin: -50px 0 0 -50px;s */
	background: #ccc;
	border-radius: 80%;
	backface-visibility: hidden;
    mix-blend-mode: exclusion;
    pointer-events: none;
    -webkit-user-select: none;
    user-select: none;
}
body.on .circle {
	z-index: 70000000000000000;
	position: absolute;
	width: 20px;
	height: 20px;
	/* top: 50%; */
	/* left: 50%; */
	/* margin: -50px 0 0 -50px;s */
	background: #7cffb1;
	border-radius: 80%;
	backface-visibility: hidden;
}
@media only screen and (max-width:435px) {
	.circle {
		display: none;
	}
}

