#header .tab {
	position: absolute;
	right: 50px;
	top: 50%;
	transform: translateY(-50%);
	z-index: 5000;
	width: 30px;
	height: 23px;
}
#header .tab span {
	position: absolute;
	right: 0;
	top: 0;
	height: 2px;
	background-color: #000;
	transition: 0.3s;
}
body.on #header .tab span{
	background-color: #fff;
}
#header .tab.dark span {
	background-color: #333;
}
#header .tab span:nth-child(1) {
	width: 21px;
}
#header .tab span:nth-child(2) {
	top: 10px;
	width: 21px;
}
#header .tab span:nth-child(3) {
	top: 20px;
	width: 30px;
}
#header .tab.active span {
	background-color: #fff;
}
#header .tab.active span:nth-child(1) {
	top: 10px;
	width: 30px;
	transform: rotate(45deg);
}
#header .tab.active span:nth-child(2) {
	display: none;
}
#header .tab.active span:nth-child(3) {
	top: 10px;
	width: 30px;
	transform: rotate(-45deg);
}
#header .total {
	display: none;
	position: fixed;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	z-index: 4000;
	background-color: rgba(0,0,0,.9);
}
#header .total.active {
	display: block;
} 
#header .total .total_inner {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 100%;
	height: 100%;
	position: relative;
}
#header .total .total_inner > ul {
	display: flex;
	flex-direction: row;
}
#header .total .total_inner > ul > li {
	margin-left: 50px;
}
#header .total .total_inner > ul > li:first-child {
	margin-left: 0;
}
#header .total .total_inner > ul > li > a {
	text-transform: uppercase;
	letter-spacing: 1px;
	font-size: 1em;
	font-weight: 500;
	color: #B7B4B4;
	transition: color 0.3s;
}
#header .total .total_inner > ul > li.active > a {
	color:#7cffb1;
	border-bottom: 1px solid #7cffb1;
	/* color:#fff;
	border-bottom: 1px solid #fff; */
}
#header .total .total_inner.on > ul > li > a {
	color: #fff;
}
#header .inner .logo h1 a {
    letter-spacing: 10px;
	font-size: 36px; /**/
	font-family: "Tourney", sans-serif;
	color: #000;
}
body.on #header .inner .logo h1 a {
	color: #fff;
}
